<template>
    <div class="m-image-crop">
        <img class="show-img"
             :src="currentValue | fileSrc">
        <svg v-if="hasRemove"
             @click.stop="$emit('remove');"
             class="icon icon-remove"
             aria-hidden="true">
            <use xlink:href="#icon-remove"></use>
        </svg>
        <slot></slot>
        <input v-show="state==1"
               class="path"
               @change="imageChange"
               ref="path"
               accept="image/*"
               type="file">
        <div v-show="state==2"
             ref="fileView"
             class="file-view"
             v-touch:move="move"
             v-touch:end="end"
             v-touch:scale="scale">
            <img class="file"
                 ref="file"
                 :src="url"
                 alt="">
            <div class="imgView"
                 ref="imgView">
                <div class="imgViewBox"
                     ref="imgViewBox">
                    <img class="file"
                         ref="file2"
                         :src="url"
                         alt="">
                </div>
            </div>
            <div layout="row"
                 layout-align="space-between stretch"
                 class="ctrl">
                <div class="back"
                     flex="33"
                     @click="back"
                     layout="column"
                     layout-align="center center">åæ¶</div>
                <div class="rotate"
                     @click="rotateChange"
                     flex="33"
                     layout="column"
                     layout-align="center center">æè½¬</div>
                <div class="submit"
                     flex="33"
                     @click="submitFile"
                     layout="column"
                     layout-align="center center">ç¡®è®¤</div>
            </div>
        </div>
    </div>
</template>
<script>
/*
 value{}
 hasRemove: æ¯å¦æ¾ç¤ºç§»é¤æé®
 config{
    size:1200 åºå®å¾çå®½åº¦  1200é»è®¤
    isSlice: true å¤æ­å¾çæ¯å¦ç¬¦åæ¯ä¾ï¼æ¯å¦ç´æ¥ä¸ä¼ 
 }æ­¤å¯¹è±¡ä¸ä¼ æé»è®¤ï¼ä¼ æ¶å¯¹è±¡éçæ¯ä¸ªå±æ§å¿é¡»é½ä¼ 
 proportion:{w: 2,h: 1}é¿å®½æ¯
 quality æ¸æ°åº¦é»è®¤1
 immediatelyUploadï¼false æ¯å¦ç´æ¥ä¸ä¼  false  ture
 å±å¹æè½¬ææªè°è¯

 event
 removeï¼ç§»é¤æé®è¢«ç¹å»æ¶è§¦å
 changeï¼æ¹åéä¸­å¾çæ¶è§¦å
 submitï¼å¾çæä½å®ææ¶è§¦å
 cancelï¼å¾çæä½åæ¶æ¶è§¦å
* */
export default {
    name: 'm-image-crop',
    data() {
        return {
            state: 1, // æªå¾ç¶æ
            top: null, // åå§ç§»å¨Y
            left: null, // åå§ç§»å¨X
            url: null, // å¾çå°å
            img: new Image(),
            maxSize: 3, // æå¤§ç¼©æ¾æ¯
            initViewWidth: 74 / 100, // åå§æªååºå®½åº¦æ¯ä¾
            borderWidth: 1, // æªååºborder
            view: { width: 0, height: 0 }, // å±å¹
            fileSize: { width: 0, height: 0, top: 0, left: 0 }, // å¾ç
            fileInitSize: { width: 0, height: 0 }, // å¾çåå§å¤§å°
            filRealitySize: { width: 0, height: 0 }, // å¾ççå®å¤§å°
            viewBox: { width: 0, height: 0, left: 0, top: 0 }, // æªååº
            rotate: 0, // æè½¬è§åº¦ 0 1 2 3
            point1: null, // è§¦æ§ç¹1
            point2: null // è§¦æ§ç¹2
        };
    },
    props: {
        value: {
            type: Object
        },
        proportion: {
            type: Object,
            default: function() {
                // æ¯ä¾
                return { w: 2, h: 1 };
            }
        },
        quality: {
            type: Number,
            default: 0.98
        },
        hasRemove: {
            type: Boolean,
            default: false
        },
        immediatelyUpload: {
            type: Boolean,
            default: false
        },
        config: {
            type: Object,
            default: function() {
                return {
                    size: 1200, // å¾çæå¤§å®½åº¦ï¼pxï¼
                    isSlice: true, // æ¯å¦éè¦è£åª
                    path: null // æ¾ç¤ºå¾çæ¶çè·¯å¾ï¼pathåæ°å°ç´æ¥ä¸å¾çidæ¼æ¥(path + id)
                };
            }
        }
    },
    components: {},
    beforeDestroy() {
        document.removeEventListener('touchmove', this.preventDefault, false);
        document.body.removeChild(this.$refs.fileView); // éåºéæ¯å¤ç½®æªååºå
    },
    mounted() {
        // åå§åvalueæ ¼å¼
        document.body.appendChild(this.$refs.fileView); // å¤ç½®æªååºå
        this.currentValue.origin = {};
        this.$nextTick(function() {
            this.initViewSize();
        });
    },
    methods: {
        imageChange(evt) {
            // å è½½æä»¶
            let file = evt.target.files[0];
            this.$emit('change', file);
            if (!file) return;
            let url = URL.createObjectURL(file);
            this.currentValue.origin = file;
            // æ¸²æå¾ç
            this.img.src = url;

            this.img.onload = () => {
                this.filRealitySize = {
                    width: this.img.width,
                    height: this.img.height
                };

                let showView = () => {
                    this.$indicator.open();
                    this.loadImg();
                    setTimeout(() => {
                        this.$indicator.close();
                        this.state = 2;
                        this.$nextTick(function() {
                            this.file2SizePosition();
                        });
                    }, 800);
                };
                if (this.immediatelyUpload) {
                    this.uploadCanvas();
                } else {
                    if (
                        this.config.isSlice &&
                        this.filRealitySize.width /
                            this.filRealitySize.height ==
                            this.proportion.w / this.proportion.h
                    ) {
                        var flag = window.confirm(
                            'æä»¶å·²ç¬¦åè§å®å°ºå¯¸ï¼æ¯å¦ç´æ¥ä¸ä¼ ï¼'
                        );
                        if (flag) {
                            this.uploadCanvas();
                        } else {
                            showView();
                        }
                    } else {
                        showView();
                    }
                }
            };
        },
        loadImg() {
            let imgWidth = this.filRealitySize.width;
            if (imgWidth > this.config.size) {
                imgWidth = this.config.size;
            }
            let imgHeight =
                imgWidth *
                (this.filRealitySize.height / this.filRealitySize.width);
            // è§£å³å¼å®¹ ios
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            switch (this.rotate) {
                case 0:
                    canvas.setAttribute('width', imgWidth);
                    canvas.setAttribute('height', imgHeight);
                    ctx.drawImage(this.img, 0, 0, imgWidth, imgHeight);
                    break;
                case 1:
                    imgWidth += imgHeight;
                    imgHeight = imgWidth - imgHeight;
                    imgWidth = imgWidth - imgHeight;
                    canvas.setAttribute('width', imgWidth);
                    canvas.setAttribute('height', imgHeight);
                    ctx.translate(imgWidth, 0);
                    ctx.rotate(90 * Math.PI / 180);
                    ctx.drawImage(this.img, 0, 0, imgHeight, imgWidth);
                    break;
                case 2:
                    canvas.setAttribute('width', imgWidth);
                    canvas.setAttribute('height', imgHeight);
                    ctx.translate(imgWidth, imgHeight);
                    ctx.rotate(180 * Math.PI / 180);
                    ctx.drawImage(this.img, 0, 0, imgWidth, imgHeight);
                    break;
                case 3:
                    imgWidth += imgHeight;
                    imgHeight = imgWidth - imgHeight;
                    imgWidth = imgWidth - imgHeight;
                    canvas.setAttribute('width', imgWidth);
                    canvas.setAttribute('height', imgHeight);
                    ctx.translate(0, imgHeight);
                    ctx.rotate(270 * Math.PI / 180);
                    ctx.drawImage(this.img, 0, 0, imgHeight, imgWidth);
                    break;
            }
            this.url = canvas.toDataURL(
                this.currentValue.origin.type,
                this.quality
            );
            if (imgWidth / imgHeight >= this.view.width / this.view.height) {
                // å¤æ­å¾çé¿å®½æ¯
                let _fileSize = {
                    width: this.view.width,
                    height: imgHeight / imgWidth * this.view.width
                };
                if (_fileSize.height < this.viewBox.height) {
                    // å¤æ­æ¯å¦å°äºæªååº
                    _fileSize.height = this.viewBox.height;
                    _fileSize.width = imgWidth / imgHeight * _fileSize.height;
                }
                this.fileSize.width = _fileSize.width;
                this.fileSize.height = _fileSize.height;
                this.fileSize.left = 0;
                this.fileSize.top =
                    (this.view.height - this.fileSize.height) / 2;
            } else {
                let _fileSize = {
                    width: imgWidth / imgHeight * this.view.height,
                    height: this.view.height
                };
                if (_fileSize.width < this.viewBox.width) {
                    _fileSize.width = this.viewBox.width;
                    _fileSize.height = imgHeight / imgWidth * _fileSize.width;
                }
                this.fileSize.height = _fileSize.height;
                this.fileSize.width = _fileSize.width;
                this.fileSize.top = 0;
                this.fileSize.left =
                    (this.view.width - this.fileSize.width) / 2;
            }
            this.fileInitSize = {
                width: this.fileSize.width,
                height: this.fileSize.height
            };
            this.fileSizePosition();
        },
        initViewSize() {
            this.view.width = document.documentElement.clientWidth;
            this.view.height = document.documentElement.clientHeight;
            // å±å¹
            this.$refs.fileView.style.width = this.view.width + 'px';
            this.$refs.fileView.style.height = this.view.height + 'px';
            // ä¸»å·¥ä½åº
            this.$refs.imgView.style.width = this.view.width + 'px';
            this.$refs.imgView.style.height = this.view.height + 'px';
            // è§å¾åº
            if (
                this.view.width *
                    this.initViewWidth *
                    this.proportion.h /
                    this.proportion.w <
                this.view.height * this.initViewWidth
            ) {
                // å¤æ­å®½é«
                this.viewBox = {
                    width: Math.floor(this.view.width * this.initViewWidth),
                    height: Math.floor(
                        this.view.width *
                            this.initViewWidth *
                            this.proportion.h /
                            this.proportion.w
                    )
                };
            } else {
                this.viewBox = {
                    width: Math.floor(
                        this.view.height *
                            this.initViewWidth *
                            this.proportion.w /
                            this.proportion.h
                    ),
                    height: Math.floor(this.view.height * this.initViewWidth)
                };
            }

            this.viewBox.left = Math.floor(
                (this.view.width - this.viewBox.width) / 2
            );
            this.viewBox.top = Math.floor(
                (this.view.height - this.viewBox.height) / 2
            );

            this.$refs.imgViewBox.style.width = this.viewBox.width + 'px';
            this.$refs.imgViewBox.style.height = this.viewBox.height + 'px';
            this.$refs.imgViewBox.style.top = this.viewBox.top + 'px';
            this.$refs.imgViewBox.style.left = this.viewBox.left + 'px';
        },
        fileSizePosition() {
            this.$refs.file.style.top = this.fileSize.top + 'px';
            this.$refs.file.style.left = this.fileSize.left + 'px';
            this.$refs.file.style.width = this.fileSize.width + 'px';
            this.$refs.file.style.height = this.fileSize.height + 'px';
            this.file2SizePosition();
        },
        file2SizePosition() {
            this.$refs.file2.style.top =
                this.$refs.file.offsetTop -
                (this.$refs.imgViewBox.offsetTop + this.borderWidth) +
                'px';
            this.$refs.file2.style.left =
                this.$refs.file.offsetLeft -
                (this.$refs.imgViewBox.offsetLeft + this.borderWidth) +
                'px';
            this.$refs.file2.style.width = this.$refs.file.offsetWidth + 'px';
            this.$refs.file2.style.height = this.$refs.file.offsetHeight + 'px';
        },
        setAttr(ele, attrs) {
            // ææªç¨å°
            if (!attrs && attrs.length < 1) {
                return;
            }
            // attrs.forEach((item, key)=>{
            //     console.log(item);
            //     console.log(key);
            // });
        },
        checkPosition() {
            // ç¼©æ¾ç§»å¨æ ¡éª
            let sizeFlag = false;
            if (this.fileSize.top >= this.viewBox.top + this.borderWidth) {
                // top æ ¡éª
                this.fileSize.top = this.viewBox.top + this.borderWidth;
            } else if (
                this.fileSize.top + this.fileSize.height <=
                this.viewBox.top + this.viewBox.height + this.borderWidth
            ) {
                this.fileSize.top =
                    this.viewBox.top +
                    this.viewBox.height -
                    this.fileSize.height +
                    this.borderWidth;
            }
            if (this.fileSize.left >= this.viewBox.left + this.borderWidth) {
                // left æ ¡éª
                this.fileSize.left = this.viewBox.left + this.borderWidth;
            } else if (
                this.fileSize.left + this.fileSize.width <=
                this.viewBox.left + this.viewBox.width + this.borderWidth
            ) {
                this.fileSize.left =
                    this.viewBox.left +
                    this.viewBox.width -
                    this.fileSize.width +
                    this.borderWidth;
            }
            // ç¼©æ¾å¤§å°æ ¡éª
            if (this.fileSize.width <= this.viewBox.width) {
                // width æ ¡éª
                this.fileSize.width = this.viewBox.width;
                this.fileSize.height =
                    this.fileInitSize.height /
                    this.fileInitSize.width *
                    this.fileSize.width;
            }
            if (this.fileSize.height <= this.viewBox.height) {
                this.fileSize.height = this.viewBox.height;
                this.fileSize.width =
                    this.fileInitSize.width /
                    this.fileInitSize.height *
                    this.fileSize.height;
            }
            if (this.fileSize.width > this.fileInitSize.width * this.maxSize) {
                this.fileSize.width = this.fileInitSize.width * this.maxSize;
                this.fileSize.height =
                    this.fileInitSize.height /
                    this.fileInitSize.width *
                    this.fileSize.width;
                sizeFlag = true;
            }
            if (
                this.fileSize.height >
                this.fileInitSize.height * this.maxSize
            ) {
                this.fileSize.height = this.fileInitSize.height * this.maxSize;
                this.fileSize.width =
                    this.fileInitSize.width /
                    this.fileInitSize.height *
                    this.fileSize.height;
                sizeFlag = true;
            }
            if (sizeFlag) {
                this.fileSize.left = this.$refs.file.offsetLeft; // é»æ­¢ç§»å¨
                this.fileSize.top = this.$refs.file.offsetTop;
            }
        },
        move(evt) {
            if (this.top) {
                this.fileSize.top = Math.floor(
                    this.$refs.file.offsetTop +
                        evt.touches[0].clientY -
                        this.top
                );
                this.fileSize.left = Math.floor(
                    this.$refs.file.offsetLeft +
                        evt.touches[0].clientX -
                        this.left
                );
                this.checkPosition();
                this.fileSizePosition();
            }
            this.top = evt.touches[0].clientY;
            this.left = evt.touches[0].clientX;
        },
        scale(evt) {
            if (this.point1) {
                let scale = this.distance(evt.touches);
                let oldSize = {
                    width: this.fileSize.width,
                    height: this.fileSize.height
                };
                let newSize = {
                    width: oldSize.width * scale,
                    height: oldSize.height * scale
                };
                this.fileSize.width = newSize.width;
                this.fileSize.height = newSize.height;
                this.fileSize.left =
                    this.$refs.file.offsetLeft -
                    (newSize.width - oldSize.width) / 2;
                this.fileSize.top =
                    this.$refs.file.offsetTop -
                    (newSize.height - oldSize.height) / 2;
                this.checkPosition();
                this.fileSizePosition();
            }
            this.point1 =
                JSON.stringify(evt.touches[0]).length < 3
                    ? evt.touches[0]
                    : JSON.parse(JSON.stringify(evt.touches[0]));
            this.point2 =
                JSON.stringify(evt.touches[1]).length < 3
                    ? evt.touches[1]
                    : JSON.parse(JSON.stringify(evt.touches[1]));
        },
        distance(touches) {
            let oldVal = Math.sqrt(
                Math.pow(touches[0].clientX - touches[1].clientX, 2) +
                    Math.pow(touches[0].clientY - touches[1].clientY, 2)
            );
            let newVal = Math.sqrt(
                Math.pow(this.point1.clientX - this.point2.clientX, 2) +
                    Math.pow(this.point1.clientY - this.point2.clientY, 2)
            );
            let scale = oldVal / newVal;
            return scale;
        },
        imgSizeScale() {
            let sizeScale;
            if (
                this.viewBox.width /
                    this.$refs.file.offsetWidth *
                    this.filRealitySize.width <
                this.config.size
            ) {
                sizeScale = Number(
                    (
                        this.viewBox.width /
                        this.$refs.file.offsetWidth *
                        this.filRealitySize.width /
                        this.viewBox.width
                    ).toFixed(15)
                );
            } else {
                sizeScale = this.config.size / this.viewBox.width;
            }
            return sizeScale;
        },
        setCanvas() {
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            let sizeScale = this.imgSizeScale(); // åºå®å¤§å°å®½åº¦æ¯å¼
            canvas.setAttribute('width', this.viewBox.width * sizeScale);
            canvas.setAttribute('height', this.viewBox.height * sizeScale);
            ctx.drawImage(
                this.$refs.file,
                (this.$refs.file.offsetLeft -
                    this.viewBox.left -
                    this.borderWidth) *
                    sizeScale,
                (this.$refs.file.offsetTop -
                    this.viewBox.top -
                    this.borderWidth) *
                    sizeScale,
                this.$refs.file.offsetWidth * sizeScale,
                this.$refs.file.offsetHeight * sizeScale
            );
            // let base64 = canvas.toDataURL(this.currentValue.origin.type, this.quality);
            let base64 = canvas.toDataURL('image/jpeg', this.quality);
            this.$set(this.currentValue, 'base64', base64);
            // this.currentValue.content = base64.split(',')[1];
            this.currentValue.id = null;
            this.$emit('submit', this.currentValue);
        },
        uploadCanvas() {
            // ä¸è£åªï¼ç´æ¥ä¸ä¼ 
            let imgWidth = this.filRealitySize.width;
            if (imgWidth > this.config.size) {
                imgWidth = this.config.size;
            }
            let imgHeight =
                imgWidth *
                (this.filRealitySize.height / this.filRealitySize.width);
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            canvas.setAttribute('width', imgWidth);
            canvas.setAttribute('height', imgHeight);
            ctx.drawImage(this.img, 0, 0, imgWidth, imgHeight);
            // let _base64 = canvas.toDataURL(this.currentValue.origin.type, this.quality);
            let base64 = canvas.toDataURL('image/jpeg', this.quality);
            this.$set(this.currentValue, 'base64', base64);
            // this.currentValue.content = base64.split(',')[1];
            this.currentValue.id = null;
            this.$emit('submit', this.currentValue);
        },
        end() {
            this.top = null;
            this.left = null;
            this.point1 = null;
            this.point2 = null;
        },
        preventDefault(e) {
            e.preventDefault();
        },
        addEventDefault() {
            document.addEventListener('touchmove', this.preventDefault, false);
        },
        removeEventDefault() {
            document.removeEventListener(
                'touchmove',
                this.preventDefault,
                false
            );
        },
        submitFile() {
            this.state = 1;
            this.$refs.path.value = null; // éç½®éåæä»¶
            this.setCanvas();
        },
        rotateChange() {
            this.rotate++;
            this.rotate %= 4;
            this.loadImg();
        },
        back() {
            this.state = 1;
            this.$refs.path.value = null; // éç½®éåæä»¶
            this.$emit('cancel');
        }
    },
    watch: {
        state(newVal) {
            if (newVal === 2) {
                this.addEventDefault();
            } else {
                this.removeEventDefault();
            }
        }
    },
    computed: {
        currentValue: {
            get() {
                return this.value;
            },
            set(val) {
                this.$emit('input', val);
            }
        }
    },
    filters: {
        fileSrc(value) {
            if (value) {
                if (value.base64) {
                    return value.base64;
                } else if (value.id) {
                    return this.config.path + value.id;
                } else {
                    return '';
                }
            } else {
                return '';
            }
        }
    },
    directives: {
        touch: {
            bind: function(el, binding) {
                /* ä¼ å¥çæ¨¡å¼ press swipeRight swipeLeft swipeTop swipeDowm Tap move end*/
                var touchType = binding.arg;
                var timeOutEvent = 0;
                var direction = '';
                /* æ»å¨å¤ç */
                var startX, startY;

                /* è¿åè§åº¦ */
                function GetSlideAngle(dx, dy) {
                    return Math.atan2(dy, dx) * 180 / Math.PI;
                }

                /* æ ¹æ®èµ·ç¹åç»ç¹è¿åæ¹å 1ï¼åä¸ï¼2ï¼åä¸ï¼3ï¼åå·¦ï¼4ï¼åå³,0ï¼æªæ»å¨ */
                function GetSlideDirection(startX, startY, endX, endY) {
                    var dy = startY - endY;
                    var dx = endX - startX;
                    var result = 0;

                    /* å¦ææ»å¨è·ç¦»å¤ªç­ */
                    if (Math.abs(dx) < 2 && Math.abs(dy) < 2) {
                        return 'tap';
                    }

                    var angle = GetSlideAngle(dx, dy);
                    if (angle >= -45 && angle < 45) {
                        result = 'swiperight';
                    } else if (angle >= 45 && angle < 135) {
                        result = 'swipeup';
                    } else if (angle >= -135 && angle < -45) {
                        result = 'swipedown';
                    } else if (
                        (angle >= 135 && angle <= 180) ||
                        (angle >= -180 && angle < -135)
                    ) {
                        result = 'swipeleft';
                    }
                    return result;
                }

                el.addEventListener(
                    'touchstart',
                    function(ev) {
                        startX = ev.touches[0].pageX;
                        startY = ev.touches[0].pageY;

                        /* å¤æ­é¿æ */
                        timeOutEvent = setTimeout(() => {
                            timeOutEvent = 0;
                            if (touchType === 'press') {
                                binding.value(ev);
                            }
                        }, 500);
                    },
                    false
                );

                el.addEventListener('touchmove', function(ev) {
                    clearTimeout(timeOutEvent);
                    timeOutEvent = 0;
                    if (ev.touches.length === 1) {
                        if (touchType === 'move') {
                            binding.value(ev);
                        }
                    } else if (event.touches.length === 2) {
                        if (touchType === 'scale') {
                            binding.value(ev);
                        }
                    }
                });

                el.addEventListener(
                    'touchend',
                    function(ev) {
                        var endX, endY;
                        endX = ev.changedTouches[0].pageX;
                        endY = ev.changedTouches[0].pageY;
                        direction = GetSlideDirection(
                            startX,
                            startY,
                            endX,
                            endY
                        );

                        clearTimeout(timeOutEvent);
                        if (touchType === 'end') {
                            binding.value(ev);
                        }
                        switch (direction) {
                            case 0:
                                break;
                            case 'tap':
                                if (touchType === 'tap') {
                                    binding.value(ev);
                                }
                                break;
                            case 'swipeup':
                                if (touchType === 'swipeup') {
                                    binding.value(ev);
                                }
                                break;
                            case 'swipedown':
                                if (touchType === 'swipedown') {
                                    binding.value(ev);
                                }
                                break;
                            case 'swipeleft':
                                if (touchType === 'swipeleft') {
                                    binding.value(ev);
                                }
                                break;
                            case 'swiperight':
                                if (touchType === 'swiperight') {
                                    binding.value(ev);
                                }
                                break;
                            default:
                                break;
                        }
                    },
                    false
                );
            }
        }
    }
};
</script>

